import React from "react"; //This statement imports react from the react object!
import Titles from "./components/Titles";
import Form from "./components/Form";
import Weather from "./components/Weather";

//const API_KEY = "7921124a7a140b113d4142db0d1c7976"
class App extends React.Component {
  state ={
    temperature: undefined,
    city: undefined,
    country: undefined,
    description: undefined,
    error: undefined
  }
  getWeather = async (e) =>{
    e.preventDefault();
    const city= e.target.elements.city.value;
    const country = e.target.elements.country.value;
    const api_call = await fetch(`http://api.openweathermap.org/data/2.5/weather?q=${city},${country}&mode=json&appid=7921124a7a140b113d4142db0d1c7976&units=metric`);
  /*
    Now we wanna convert the data we get into JSON, so that it can be read from the api.
    States arre something that keeps track ofthe change of data.
  
    */


 const data = await api_call.json();
    if(country && city){
  console.log(data);
 this.setState({
   temperature: data.main.temp,
   city: data.name,
   country: data.sys.country,
   humidity: data.main.humidity,
   description: data.weather[0].description,
   error: "Absolutely No Error!"
 });
  }
  else{
    console.log(data);
    this.setState({
      temperature: undefined,
      city: undefined,
      country: undefined,
      humidity: undefined,
      description: undefined,
      error: "Please enter the value."
    });
  }
}

/*else{
  this.setState({
    temperature: undefined,
    city: undefined,
    country: undefined,
    humidity: undefined,
    description: undefined,
    error: "Please Enter the country and date."
  });
}*/
    /*
      Arrow function allow you too use the this keyword independently. This indicated that it is bound to that 
      component


      getWeather is a prop.
    */
   
  render(){
    return(
      <div>
        <div className="wrapper">
          <div className="main">
            <div className="container">
              <div className="row">
                <div className="col-xs-5 title-container">
                  <Titles/>
                </div>
                <div className="col-xs-7 form-container">
                <Form getWeather={this.getWeather}/>
                  <Weather 
                    temperature={this.state.temperature}
                    city = {this.state.city}
                    country={this.state.country}
                    humidity={this.state.humidity}
                    description = {this.state.description}
                    error={this.state.error}
                  />
                </div>
              </div>
            </div>
          </div>
          
        </div>
        <h3 className=" footer panel panel-heading ">Copyright Â©: Raghav Maheshwari</h3>
      </div>
    )
  }
}

export default App;


